.admin__container {
  min-height: calc(100vh - 162px);
}
.admin__btn {
  width: 350px;
  height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ddd;
}

.admin__user__search {
  width: 100%;
  border-collapse: collapse;
  font-size: 20px;
  padding: 0 20px;
}
.admin__user__search .filter__checkboxes input + label {
  font-size: 20px;
}
.admin__user__search td {
  padding: 6px 0;
}
.admin__user__search input {
  max-width: 1000px;
  width: 100%;
  height: 85px;
  padding: 0 20px;
  line-height: 85px;
  font-size: 20px;
  border: 1px solid #ddd;
  border-radius: 20px;
}

.admin__ads-tabHeader {
  height: 160px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  background: #b6b8bf;
}
.admin__ads-tabHeader.bg-purple {
  background: #223f55;
}
.admin__ads-tabHeader h1 {
  font: normal normal 600 24px/24px Montserrat;
  margin-bottom: 15px;
}
.admin__ads-tabHeader p {
  font: normal normal normal 19px/24px Montserrat;
  margin: 0;
}
.admin__ads-tabContent {
  padding: 50px 0;
}
.admin__ads-tabContent.side-ad {
  display: flex;
  flex-wrap: wrap;
  gap: 2%;
}
.admin__ads-item {
  width: 100%;
  max-height: 140px;
  display: flex;
  position: relative;
  margin-bottom: 20px;
}
.admin__ads-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.admin__ads-img img {
  max-height: 100%;
  max-width: 100%;
}
.admin__ads-removeBtn {
  width: 160px;
  color: #f05454;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  cursor: pointer;
}

.admin__ads-item-side {
  width: calc(32%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 20px;
}
.admin__ads-item-side .admin__ads-img {
  max-height: 390px;
}
.admin__ads-item-side .admin__ads-removeBtn {
  width: 100%;
  height: 60px;
}

.admin-nfts {
  gap: 10px;
}
.admin-nfts > div {
  width: 280px;
}
.admin__nfts-remove {
  font-size: 20px;
  color: red;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
}

.redeem-withdraw-box {
  border: 1px solid #333;
  border-radius: 12px;
  flex-direction: column;
  padding: 20px;
  max-width: 600px;
  width: 100%;
  margin: 50px auto 0;
}
.redeem-withdraw-box input {
  font-size: 16px !important;
}
