.miner__about {
  padding: 270px 0 120px;
  overflow: hidden;
}

.miner__about--content {
  max-width: 810px;
}
.miner__about--image img {
  width: 100%;
  min-width: 270px;
}

.miner__about--content h3 {
  width: fit-content;
  font: normal normal 900 36px/73px Montserrat;
  letter-spacing: 0px;
  color: #343a40;
  margin-bottom: 45px;
  padding-top: 20px;
  margin-top: -20px;
  padding-right: 70px;
  margin-right: -70px;
  background: url("../../assets/img/miner/wave.png") top right no-repeat;
}
.miner__about--content h3 span {
  font-size: 16px;
  color: white;
  background: #343a40;
  padding: 5px 20px;
  border-radius: 20px;
}
.miner__about--content p {
  font: var(--unnamed-font-style-normal) normal normal
    var(--unnamed-font-size-18) / 40px var(--unnamed-font-family-montserrat);
  letter-spacing: var(--unnamed-character-spacing-0);
  color: var(--unnamed-color-343a40);
  font: normal normal normal 18px/40px Montserrat;
  letter-spacing: 0px;
  color: #343a40;
  margin-bottom: 75px;
}

.miner__about--content a {
  width: 300px;
  height: 125px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 20px;
  color: white;
  border-radius: 8px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
}

.bg-primary {
  background-color: #223f55;
}
.bg-secondary {
  background-color: #223f55;
}

.miner__confirm--text {
  background: #e9ecef;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 40px;
}
.miner__confirm--text p {
  color: #444;
  font-size: 18px;
  margin-bottom: 150px;
}
.miner__confirm--text input {
  font-size: 32px;
  color: black;
  text-align: right;
  width: 100%;
  background: transparent;
  border: none;
  outline: none;
  font-weight: 900;
  padding-right: 15px;
}
.miner__confirm button {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  border-radius: 6px;
}
.miner__confirm button.bg-primary {
  color: white;
}

.miner__confirm-digging {
  margin: 130px 0;
}
.miner__confirm-digging img {
  max-width: 100%;
  width: auto;
}
.miner__confirm-digging p {
  text-align: left;
  font: normal normal bold 47px/143px Montserrat;
  letter-spacing: 0px;
  color: #223f55;
}
.miner__confirm-digging input {
  outline: none;
  border: none;
  text-align: right;
  padding: 0 20px;
  font: normal normal 600 47px/143px Montserrat;
  letter-spacing: 0px;
  color: #343a40;
  border-radius: 10px;
  width: 100%;
  background: #e9ecef;
}

.miner__faq-item h3 {
  font-family: Montserrat;
  font-size: 20px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  border-radius: 8px;
  color: white;
}
.miner__faq-item p {
  text-align: left;
  font: normal normal normal 18px/22px Montserrat;
  letter-spacing: 0px;
  color: #343a40;
  margin: 48px 0 65px;
}

.bg-green-gradient {
  background: linear-gradient(90deg, #6fa8b1 0, #223f55 100%);
  width: 350px;
  height: 100px;
  border: none;
  outline: none;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 900;
}
.bg-green-gradient:hover {
  background: linear-gradient(90deg, #223f55 0, #6fa8b1 100%);
  color: black;
}
.bg-green-gradient:hover span {
  display: none;
}

@media (max-width: 1439px) {
  .miner__about {
    padding-top: 150px;
  }
}
@media (max-width: 767px) {
  .miner__about {
    text-align: center;
  }
  .miner__about--content h3 {
    padding-right: 0;
    margin-right: 0;
  }
  .miner__confirm-digging img {
    width: 200px;
  }
  .miner__confirm-digging p {
    line-height: 1.5;
    margin-top: 40px;
    font-size: 30px;
  }
  .miner__confirm-digging input {
    line-height: 60px;
    height: 60px;
    font-size: 24px;
  }
}
.footer-social-link a {
  transition: transform 0.5s ease;
}

.footer-social-link a:hover {
  transform: scale(1.3);
}
