.stake {
  padding: 200px 0 100px;
}

.stake img {
  width: 100%;
}
.stake__right {
  padding: 0 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.stake__right h1 {
  color: #23262f;
  font: normal normal 900 60px/73px Montserrat;
}
.stake__right p {
  margin: 50px 0;
  font: normal normal normal 18px/22px Montserrat;
  color: #141416;
}
.stake__right button {
  width: 275px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  background: #223f55;
  color: white;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
  border-radius: 6px;
  font-size: 12px;
}
