.footer {
  background-color: #dddddd;
  padding: 30px 0 20px;
  position: relative;
  overflow: hidden;
}
.footer__logo {
  margin-right: 50px;
}
.footer__logo img {
  width: auto;
  display: block;
  height: 120px;
}
.footer .container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer-title {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.admin-btn {
  background: #2c6545;
  width: 150px;
  height: 50px;
  color: white;
}
.btn-disclaimer {
  font-family: Montserrat;
  font-size: 18px;
  letter-spacing: 0px;
  color: #23262f;
  cursor: pointer;
}
.footer-title h1 {
  font-size: 24px;
}
.footer-title p {
  font-size: 14px;
}
.footer .container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 576px) {
  .footer {
    padding: 30px 0 20px;
  }
}
@media (min-width: 768px) {
  .footer-title {
    align-items: flex-end;
  }
  .footer .container {
    flex-direction: row;
  }
  .footer {
    padding: 50px 0 55px;
  }
  .footer-title h1 {
    font-size: 35px;
  }
  .footer-title p {
    font-size: 20px;
  }
}
@media (min-width: 1200px) {
  .admin-btn {
    width: 250px;
    height: 100px;
  }
  .btn-disclaimer {
    font-size: 24px;
  }
  .footer__logo img {
    height: 200px;
  }
  .footer-title h1 {
    font-size: 50px;
  }
  .footer-title p {
    font-size: 24px;
  }
}
@media (min-width: 1440px) {
  .footer-title h1 {
    font-size: 68px;
  }
  .footer-title p {
    font-size: 24px;
  }
}
