.MAR__lockportfolio {
  padding: 35px 0 105px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 22px;
  line-height: 1;
  color: #f26a6a;
}
.MAR__lockportfolio svg {
  margin-right: 10px;
}

.MAR h4 {
  font: normal normal bold 21px/21px Montserrat;
  color: #20bdff;
  margin-bottom: 18px;
  text-align: right;
}
.MAR h3 {
  font: normal normal 900 27px/27px Montserrat;
  letter-spacing: 0px;
  color: #212529;
  margin-bottom: 35px;
}
.MAR p {
  font: normal normal normal 18px/22px Montserrat;
  color: #141416;
}
.MAR h5 {
  font: normal normal normal 24px/27px Montserrat;
  color: #212529;
}
.MAR input {
  outline: none;
  border: none;
  text-align: right;
  padding: 0 20px;
  font: normal normal 600 47px/143px Montserrat;
  color: #343a40;
  border-radius: 10px;
  background: #e9ecef;
  width: 130px;
  height: 74px;
  margin-left: auto;
}

.MAR__btn {
  width: 275px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 20px;
  color: white;
  font-size: 16px;
  border-radius: 8px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
}
.MAR__btn.bg-primary {
  color: white;
}

.nft__collection {
  min-height: calc(100vh - 1060px);
}
.nft__collection h3 {
  text-align: center;
  font: normal normal bold 24px/29px Montserrat;
  letter-spacing: 0px;
  color: #23262f;
  opacity: 1;
  margin-top: 100px;
}

.nft__collection-content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.nft__collection-content > div {
  max-width: 400px;
  width: 100%;
}

@media (max-width: 767px) {
  .MAR h4,
  .MAR h3,
  .MAR p {
    text-align: center;
  }
  .MAR input {
    margin-right: auto;
    margin-bottom: 30px;
  }
  .MAR__btn {
    margin: 0 auto;
    margin-bottom: 30px;
  }
}
